.logo{
    height: 3rem;
    width: 4rem;
    object-fit: cover;
    /* margin-left: 50px; */
}

.navbar{
    box-sizing: content-box;
    background: rgb(30, 30, 30);
    height: 3.5rem;
    width: 100vw;
    max-width: 92vw;
    
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    z-index: 3;
}
.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
}
.desktopMenuListItem:hover{
    color: yellow;
    border-bottom: 3px solid yellow;
    padding-bottom: 0.5rem;
}
.active{
    color: yellow;
    padding: 0.5rem;
    border-bottom: 3px solid yellow;
}
.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
}
.desktopMenuButton{
    background: white;
    color:black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    
}
.active{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background-color: rgb(40, 40, 40);
    border-radius: 1rem;
}
.mobMenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
}
.listItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
    border: 0.1rem solid rgb(30, 30, 30);
}
@media  screen and (max-width: 720px) {
    .mobMenu{
        display: flex;
        margin-right: 1.5rem;
    }
    .desktopMenuListItem{
        display: none;
    }
    .desktopMenuButton{
        display: none;
    }
}