#intro{
    height: calc(100vh -4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
   position: relative;
}
.bg{
    position: absolute;
    top: 3rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 95vh;   
}
.introContent{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 2.3rem;
    font-weight: 100;
}
.introName{
    color: yellow;
}
.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
    
}

.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}
.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}
@media screen and (max-width: 1025px)  {
    .bg{
        margin-top: 4rem;
        right: -5vw;
        height: 80vh;
    }
    .introContent{
        font-size: 4.5vw;
    
    }
    .hello{
        font-size: 1.5rem;
    }
    
}
@media screen and (max-width:480px) {
    .bg{
        margin-top: 5rem;
        right: -40vw;
        height: 72vh;
    }
    .introPara{
        font-size: small;
        font-weight: 200px;
        letter-spacing: 0;
    }
}