#contactPage{
    min-height: calc(100vh-3rem);
    width: 100%;
    max-width:60rem ;
    margin: 0 auto;
    /* flex-wrap: wrap; */
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.contactPageTitle{
    display: inline-block;
    border-bottom: 5px solid white;
    margin-top: 2rem;
    font-size: 3rem;
    margin-bottom: 1rem;
   
}
.clientDesc{
    font-weight: 300;
    font-size: 1rem;
    margin-bottom: 2rem;
    margin: 2rem;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}
.clientImgs{
    margin: 1rem 1rem;
    display: flex;
    flex-wrap: wrap;
    border: 1px  solid rgb(51, 50, 50);
}
.clientImg{
    object-fit:cover;
    width: 23%;
    min-width: 9rem;
    margin: auto ;
    padding: 0.25rem;
    
    
}

#clients{
    padding:2rem ;
}
.contactDesc{margin: 0 1rem;
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}
.contactForm{
    margin: 1rem 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}
.name, .email, .msg{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: rgb(40, 40, 40);
}
.submitBtn{
    background: white;
    border: none;
    padding: 0.75rem 3.5rem;
    border-radius: 0.5rem;
    margin: 2rem;
}
.links{
    margin-top: 1rem;
    display: flex;
    flex-wrap:wrap;
    justify-content: center;
    align-content: center;
}
.link{
   
    display: block;
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
    margin-bottom: 1rem;
}
